<template>
	<view class="box">
		<view class="information">
			<view class="name">
				<label for="">姓名</label>
				<input type="text" v-model="formData.name" placeholder="请输入姓名">
			</view>
			<view class="name">
				<label for="">手机号</label>
				<input type="text" v-model="formData.phone" placeholder="请输入手机号码">
			</view>
			<view class="address">
				<view>
					<label for="">地址</label>
					<city-picker class="picker" @change="onPickerChange" :range="range" :value="value">
						<view>
							<input class="" type="text" disabled="true" v-model="value" placeholder="请选择地址">
						</view>
					</city-picker>
				</view>
				<image src="@/static/img/icon_go@2x.png" mode=""></image>
			</view>
			<view class="name">
				<label for="">详细地址</label>
				<input type="text" v-model="formData.address" placeholder="请输入详细地址">
			</view>
		</view>
		<view class="set-default">
			<view class="">设为默认地址</view>
			<switch :checked="formData.isDefault" @change="switchChange" color="#00D08F" style="transform:scale(0.8)" />
		</view>
		<view class="bottom">
			<view class="cancel" @click="handleCancel">取消</view>
			<view class="save" @click="handleSave">保存地址</view>
		</view>
	</view>
</template>

<script>
	import CityPicker from '@/components/city-picker/index.vue'
	import {
		PostAddAddressAjax,
		PostEditAddressAjax
	} from '@/apis/api/modules/user-center'
	export default {
		components: {
			CityPicker
		},
		data() {
			return {
				range: [],
				value: "",
				orderId: null,
				formData: {
					name: "",
					phone: "",
					provice: "",
					city: "",
					region: "",
					address: "",
					isDefault: 0
				}
			}
		},
		onLoad(e) {
			if (e ? e.orderId : '') {
				this.orderId = e.orderId
			}
			let addressList = uni.getStorageSync("addressList");
			let items = JSON.parse(addressList);
			this.formData.name = items.Name
			this.formData.phone = items.Phone
			this.formData.address = items.Address
			this.formData.isDefault = items.IsDefault ? 1 : 0
			this.formData.provice = items.Province
			this.formData.city = items.City
			this.formData.region = items.Region
			this.value = `${this.formData.provice}${this.formData.city}${this.formData.region}`
		},
		methods: {
			/**
			 * 获取省市区列表
			 * @param {Object} list
			 */
			onPickerChange(list) {
				// console.log(list)
				this.range = list.map(item => {
					// console.log(item)
					item = [{
						label: item.label,
						value: item.value,
					}]
					return item
				})
				this.value = `${list[0].label||""}${list[1].label||''}${list[2].label||''}`
				this.formData.provice = list[0].label
				this.formData.city = list[1].label
				this.formData.region = list[2].label

			},
			switchChange(e) {
				if (e.detail.value === true) {
					this.formData.isDefault = 1
				} else {
					this.formData.isDefault = 0
				}

			},
			handleCancel() {
				uni.navigateBack({
					delta: 1
				})
			},
			/**
			 * 判断数据是否填写
			 */
			validateForm() {
				if (!this.validForm(this.formData, [{
							key: "name",
							text: "请输入姓名"
						}, {
							key: "phone",
							text: "请输入电话号码"
						},
						{
							key: "provice",
							text: "请选择地址"
						},
						{
							key: "address",
							text: "请输入详细地址"
						}
					])) {
					return false
				}
				return true
			},
			async handleSave() {
				if (!this.validateForm()) {
					return false
				}
				if (this.orderId) {
					const result = await PostEditAddressAjax({
						AddressID: this.orderId,
						...this.formData,
						token: true
					})
					uni.showToast({
						title: result.Result,
						icon: 'none'
					})
					uni.removeStorageSync('addressList')
					setTimeout(() => {
						uni.navigateBack({
							delta: 1
						})
					}, 600)
				} else {
					const result = await PostAddAddressAjax({
						...this.formData,
						token: true
					})
					uni.showToast({
						title: result.Result,
						icon: 'none'
					})
					setTimeout(() => {
						uni.navigateBack({
							delta: 1
						})
					}, 600)
				}
			}
		}
	}
</script>

<style scoped lang="scss">
	.box {
		min-height: 100%;
		background: #F7F7F7;
	}

	.information {
		background: #fff;
		padding: 0 30rpx;
		font-size: 32rpx;
		font-weight: 400;
		color: #333333;
		line-height: 44rpx;

		.name {
			display: flex;
			justify-content: start;
			align-items: center;
			padding: 30rpx 0;
			border-bottom: 2rpx solid #EFEFEF;

			&:last-child {
				border-bottom: none;
			}
		}

		.address {
			display: flex;
			justify-content: space-between;
			align-items: center;
			padding: 30rpx 0;
			border-bottom: 2rpx solid #EFEFEF;

			view {
				display: flex;
				justify-content: space-between;
				align-items: center;
			}
		}

		label {
			width: 132rpx;
			height: 44rpx;
			padding-right: 30rpx;
		}

		image {
			width: 36rpx;
			height: 30rpx;
		}
	}

	.set-default {
		background: #fff;
		margin-top: 30rpx;
		padding: 30rpx;
		display: flex;
		justify-content: space-between;
		align-items: center;
		font-size: 32rpx;
		font-weight: 400;
		color: #333333;
	}

	.bottom {
		width: 100%;
		box-sizing: border-box;
		position: absolute;
		bottom: 0;
		padding: 30rpx;
		display: flex;
		justify-content: space-between;

		.cancel,
		.save {
			width: 330rpx;
			height: 88rpx;
			border-radius: 44rpx;
			text-align: center;
			line-height: 88rpx;
			font-size: 28rpx;
			font-weight: 500;
			color: #FFFFFF;
		}

		.cancel {
			background: #BDBDBD;
		}

		.save {
			background: #00D08F;
		}
	}
</style>
